@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');

.sa {
    width: 100%;
    height: 80vh;
    background: rgba(15, 15, 15, 0.342);
    position: relative;
  }
  
  .sa:before {
    content: '';
    position: absolute;
    background: url('../../assets/sa-submenu-bg.webp') no-repeat center top/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
  }
  

.sa {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.sa .sa-content {
    display: block;
    position: absolute;
    bottom: 10%;
    padding: 1rem;
}

.sa h1 {
    font-size: 3rem;
    font-family: 'Montserrat', sans-serif;
    color: white;
    text-align: center;
}



/* MEDIA QUERIES */

@media screen and (max-width:940px) {

.sa h1 {
    font-size: 2.5rem;
}
}

@media screen and (max-width:700px) {

    .sa h1 {
        font-size: 1.5rem;
    }
}